









export default {
	data(){
		return{
			advices : ''
		}
	},
	methods : {
		inputAdvices(e){
			this.advices = e.detail.value;
		},
		submit(){
			if(this.advices.trim() == ''){
				uni.showToast({
					icon : 'none',
					title : '请输入您的意见与建议~'
				})
			}else{
				uni.showLoading({
					title : '正在提交...'
				})
				setTimeout(() => {
					uni.hideLoading();
					uni.showToast({
						icon : 'success',
						title : '提交成功'
					})
				}, 2000)
			}
		}
	}
}



